import React from "react"
import {
    Input,
    CreateField,
    TaskFormInput,ContactFormInput
} from "../../assetss/common/ValidatorsComponents/ElementsValidators";
import {MaxLengthCreator, required} from "../../utils/validators";
import ts from "./Tasks.module.css";
import GoBackButton from "../GoBackButton";



const TaskEditForm = (props) => {

    return <div>

        <form onSubmit={props.handleSubmit}>
            <div className={ts.TaskFormWrapper}>

                <div className={ts.TaskEditFromButtons}>

                        <button className={ts.TaskFormSaveButton}>
                            save
                        </button>

                    <div className={ts.GoBackButton}>
                    <GoBackButton HandleOnClick={props.TaskEditModeOFF}/>
                    </div>

                </div>


                <div>
                    <div className={props.TaskExecutedStyleForWindow}>
                        <div className={ts.MainFormWrapper}>

                            <div>
                                <div>
                                    Completed?
                                </div>

                                <div className={ts.CompletedInput}>
                                    {CreateField("completed", "completed", [], ContactFormInput, {type: "checkbox"})}
                                </div>
                            </div>

                            <div className={ts.Title}>
                                Task title :
                                {CreateField("title", "title", [], ContactFormInput)}
                            </div>
                        </div>

                            <div className={ts.detailsWrapper}>
                                <div className={ts.startDate}>
                                    Start date :
                                    {CreateField("start date", "startDate", [], ContactFormInput)}
                                </div>

                                <div className={ts.deadline}>
                                    deadline :
                                    {CreateField("deadline", "deadline", [], ContactFormInput)}
                                </div>
                            </div>
                            <div className={ts.FormDescription}>
                                description :
                                {CreateField("description", "description", [], ContactFormInput)}
                            </div>

                        <div className={ts.detailsWrapper}>
                            <div className={ts.priority}>
                                priority :
                                {CreateField("priority", "priority", [], ContactFormInput)}
                            </div>

                            <div>
                                status :
                                {CreateField("status", "status", [], ContactFormInput)}
                            </div>
                        </div>

                        </div>
                    </div>

                </div>

        </form>
    </div>
}

export default TaskEditForm